<template>
  <img alt="Vue logo" src="./assets/my_img.jpg" width="400" />
  <h1>{{ fname_fix }} {{ lname_fix }}</h1>
  <br />
  <button @click="showData">คลิกเพื่อดูข้อมูล</button>
  <br />
  <button @click="showFname">ชื่อ</button>
  <button @click="showLname">นามสกุล</button>
  <br />
  <h3>{{ age_fix }}</h3>
  <button @click="plus">เพิ่มอายุ</button>
  <button @click="minus">ลดอายุ</button>
  <br />
  <button @click="pluss(5)">เพิ่มอายุแบบส่ง val (ทีละ 5)</button>
  <button @click.ctrl="minuss(10)">
    ลดอายุแบบส่ง val (ต้องกด Ctrl ค้างไว้)
  </button>
  <br />
  <br />
  <form>
    <label for="">ป้อนห่าเหวไรก็ได้ : </label>
    <input type="text" @input="setWTF" />
  </form>
  <h2>ห่าเหว : {{ wtf }}</h2>

  <hr />

  <form>
    <label for="">ชื่อ : </label>
    <input type="text" @input="setFname" />
    <br />
    <label for=""> นามสกุล : </label>
    <input type="text" @input="setLname" />
    <br />
    <label for="">ชื่อเล่น : </label>
    <input type="text" @input="setNickname" />
    <br />
    <label for="">อายุ : </label>
    <input type="text" @input="setAge" />
    <br />
    <label for="">วันเกิด : </label>
    <input type="date" @input="setBirth" />
    <br />
    <label for="">สาขา : </label>
    <input type="text" @input="setMajor" />
    <br />
    <label for="">ห้อง : </label>
    <input type="text" @input="setClass" />
    <br />
    <label for="">คติประจำใจ : </label>
    <input type="text" @input="setApo" />
    <br />
    <label for="">สถานศึกษาที่เรียนต่อ : </label>
    <input type="text" @input="setNextSchool" />
    <br />
    <label for="">วิชาที่ไม่ชอบ : </label>
    <input type="text" @input="setUnlikeSubject" />
  </form>

  <h2>แสดงข้อมูล</h2>
  <hr width="40%" />
  <h3>ชื่อ - นามสกุล : {{ fname }} {{ lname }}</h3>
  <h3>ชื่อเล่น : {{ nickname }}</h3>
  <h3>อายุ : {{ age }}</h3>
  <h3>วันเกิด : {{ birth }}</h3>
  <h3>สาขา : {{ major }}</h3>
  <h3>ห้อง : {{ std_class }}</h3>
  <h3>คติประจำใจ : {{ apo }}</h3>
  <h3>สถานศึกษาที่จะเรียนต่อ : {{ next_school }}</h3>
  <h3>วิชาที่ไม่ชอบ : {{ unlike_sub }}</h3>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      fname_fix: "Kanoot",
      lname_fix: "Boonsri",
      age_fix: 19,
      wtf: "",

      fname: "",
      lname: "",
      nickname: "",
      age: "",
      birth: "",
      major: "",
      std_class: "",
      apo: "",
      next_school: "",
      unlike_sub: "",
    };
  },
  methods: {
    showData() {
      alert(this.fname_fix + this.lname_fix);
    },
    showFname() {
      alert(this.fname_fix);
    },
    showLname() {
      alert(this.lname_fix);
    },
    plus() {
      this.age_fix++;
    },
    minus() {
      this.age_fix--;
    },

    pluss(val) {
      this.age_fix += val;
    },
    minuss(val) {
      this.age_fix -= val;
    },

    setWTF(event) {
      console.log(event.target.value);
      this.wtf = event.target.value;
    },

    // -----------------------------------------------------

    setFname(event) {
      this.fname = event.target.value;
    },
    setLname(event) {
      this.lname = event.target.value;
    },
    setNickname(event) {
      this.nickname = event.target.value;
    },
    setAge(event) {
      this.age = event.target.value;
    },
    setBirth(event) {
      this.birth = event.target.value;
    },
    setMajor(event) {
      this.major = event.target.value;
    },
    setClass(event) {
      this.std_class = event.target.value;
    },
    setApo(event) {
      this.apo = event.target.value;
    },
    setNextSchool(event) {
      this.next_school = event.target.value;
    },
    setUnlikeSubject(event) {
      this.unlike_sub = event.target.value;
    },

    // -----------------------------------------------------
  },
};
</script>

